import DefaultProcessor from './defaultProcessor'; // const debug = require('debug')('red: videoProvider');
export default class VideoProvider {
    constructor(config, option) {
        this.config = config;
        this.option = option;
        this.lastAppendTime = null;
        this.init();
    }
    provider(currentTime) {
        if (this.lastAppendTime === currentTime) {
            // debug('');
            return null;
        }
        this.lastAppendTime = currentTime;
        const res = this.defaultProcessor.provider(currentTime); // if (res && res.frame) {
        //   // debug(' timing: ', (currentTime / this.option.baseTime - res.frame.pts) * this.option.baseTime)
        // } else {
        //   // debug('');
        // }
        return res;
    }
    destroy() {
        this.defaultProcessor = null;
    }
    get providerState() {
        return this.defaultProcessor.performanceState;
    }
    init() {
        this.defaultProcessor = new DefaultProcessor(this.config, this.option);
    }
}
