import DefualtProcessor from './defaultProcessor';
export default class AudioProvider {
    constructor(config, option) {
        this.config = config;
        this.option = option;
        this.init();
    }
    provider(size) {
        const res = this.defaultProcessor.provider(size); // this.audioTime = res.audioTime;
        return res;
    }
    destroy() {
        this.defaultProcessor = null;
    }
    init() {
        this.defaultProcessor = new DefualtProcessor(this.config, this.option);
    }
}
